  left: 9px;
  right: auto; }

.notification-list .notify-item .notify-icon {
  float: right;
  margin-left: 10px;
  margin-right: 0; }

.notification-list .notify-item .notify-details {
  margin-left: 0;
  margin-right: 45px; }

.notification-list .profile-dropdown i {
  margin-left: 5px;
  margin-right: 0px;
  float: right; }

.dot {
  right: auto;
  left: -9px; }

.pulse {
  right: auto;
  left: 3px; }

.page-title-box .page-title-right {
  float: left; }

.content-page {
  margin-right: 240px;
  margin-left: 0; }

#sidebar-menu > ul > li > a i {
  margin: 0 3px 0 10px; }

#sidebar-menu > ul > li > a .drop-arrow {
  float: left; }
  #sidebar-menu > ul > li > a .drop-arrow i {
    margin-left: 0; }

#sidebar-menu > ul > li > ul {
  padding-right: 40px;
  padding-left: 0; }
  #sidebar-menu > ul > li > ul ul {
    padding-right: 20px;
    padding-left: 0; }

#sidebar-menu .menu-arrow {
  left: 20px;
  right: auto; }
  #sidebar-menu .menu-arrow:before {
    content: "\F141"; }

#sidebar-menu li.mm-active > a > span.menu-arrow {
  transform: rotate(-90deg); }

.enlarged .left-side-menu #sidebar-menu > ul > li > a i {
  margin-left: 20px;
  margin-right: 5px; }

.enlarged .left-side-menu #sidebar-menu > ul > li > a span {
  padding-right: 25px;
  padding-left: 0; }

.enlarged .left-side-menu #sidebar-menu > ul > li:hover > ul {
  right: 70px;
  left: auto; }

.enlarged .left-side-menu #sidebar-menu > ul ul li:hover > ul {
  right: 190px;
  margin-top: -36px; }

.enlarged .left-side-menu #sidebar-menu > ul ul li > a span.pull-right {
  left: 20px;
  right: 0; }

.enlarged .navbar-custom {
  right: 0px; }

.enlarged .content-page {
  margin-right: 70px !important;
  margin-left: 0 !important; }

.enlarged .footer {
  left: 0 !important;
  right: 70px !important; }

@media (max-width: 767.98px) {
  .content-page,
  .enlarged .content-page {
    margin-right: 0 !important; } }

@media (min-width: 1025px) {
  .navbar-custom .button-menu-mobile {
    margin-left: 0;
    margin-right: 8px; } }

/* =============
  Horizontal Menu
============= */
body[data-layout="horizontal"] .content-page {
  margin-right: 0 !important; }

body[data-layout="horizontal"] .logo-box {
  margin-right: 0px;
  margin-left: 40px; }

body[data-layout="horizontal"] .footer {
  right: 0px; }

#topnav .arrow-down:after {
  margin-left: 0px;
  margin-right: 5px; }

.navigation-menu > li {
  float: right; }
  .navigation-menu > li > a i {
    margin-right: 0;
    margin-left: 8px; }

@media (max-width: 767.98px) {
  body[data-layout="horizontal"] .navbar-custom {
    padding: 0 12px 0 0; } }

@media (min-width: 992px) {
  .navigation-menu > li:first-of-type > a {
    padding-left: 17px;
    padding-right: 0; }
  .navigation-menu > li .submenu {
    right: 0;
    left: auto;
    text-align: right; }
    .navigation-menu > li .submenu li ul {
      padding-right: 0; }
    .navigation-menu > li .submenu li.has-submenu > a .arrow-down:after {
      right: auto !important;
      left: 20px;
      transform: rotate(45deg) translateY(-50%) !important; }
    .navigation-menu > li .submenu li .submenu {
      left: auto;
      right: 100%;
      margin-left: 0;
      margin-right: 10px; }
  .navigation-menu > li.last-elements .submenu {
    left: 0;
    right: auto; } }

@media (max-width: 991px) {
  #topnav .navigation-menu {
    text-align: right; }
    #topnav .navigation-menu > li .submenu {
      padding-right: 20px;
      padding-left: 0px; }
      #topnav .navigation-menu > li .submenu.megamenu > li > ul {
        padding-right: 0; }
  #topnav .arrow-down:after {
    right: auto;
    left: 15px; } }

@media (max-width: 767.98px) {
  body[data-layout=horizontal] .logo-box {
    margin-left: 0; } }

