.card-widgets {
  float: left; }
  .card-widgets > a {
    margin-left: 0;
    margin-right: 7px; }

.icons-list-demo i {
  margin-left: 12px;
  margin-right: 0; }

.checkbox label {
  padding-right: 8px;
  padding-left: 0; }
  .checkbox label::before {
    left: auto;
    right: 0;
    margin-left: 0;
    margin-right: -18px; }
  .checkbox label::after {
    left: auto;
    right: 0;
    margin-right: -18px;
    margin-left: 0;
    padding-left: 0;
    padding-right: 3px; }

.checkbox input[type="checkbox"]:checked + label::after {
  left: auto;
  right: 7px;
  transform: rotate(45deg); }

.checkbox.checkbox-single label:before {
  margin-right: 0; }

.checkbox.checkbox-single label:after {
  margin-right: 0; }

.radio label {
  padding-left: 0;
  padding-right: 8px; }
  .radio label::before {
    left: auto;
    right: 0;
    margin-left: 0;
    margin-right: -18px; }
  .radio label::after {
    left: 0;
    right: 6px;
    margin-left: 0;
    margin-right: -20px; }

